@font-face {
  font-family: "Barlow-Regular";
  font-style: normal;
  src: url("../fonts/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular.woff") format("woff"); }

@font-face {
  font-family: "Barlow-Bold";
  font-style: normal;
  src: url("../fonts/Barlow-Bold.woff2") format("woff2"), url("../fonts/Barlow-Bold.woff") format("woff"); }

.main {
  background-color: #eff6ff;
  padding: 0;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  color: #2a4a5c; }

.main__background {
  min-width: 760px;
  min-height: 1000px;
  background-image: url("../img/bg.jpg");
  background-size: cover; }

@media (max-width: 1440px) {
  .main__background {
    display: none; } }

.main__content {
  display: flex;
  flex-direction: column;
  margin: 110px 75px;
  align-items: flex-start; }

.main__desc {
  font-size: 20px;
  margin: 0;
  margin-bottom: 80px;
  color: #7b8a92; }

.main__social {
  margin: 0;
  margin-bottom: 18px; }

.main__separator {
  align-self: center;
  font-size: 12px;
  position: relative; }
  .main__separator::before {
    content: "";
    position: absolute;
    width: 100px;
    height: 1px;
    left: -110px;
    top: 7px;
    background-color: #afafaf; }
  .main__separator::after {
    content: "";
    position: absolute;
    width: 100px;
    height: 1px;
    right: -110px;
    top: 7px;
    background-color: #afafaf; }

.main__form {
  display: flex;
  flex-direction: column;
  min-width: 530px; }

.main__list-of-types {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0; }

.main__email {
  color: #484848;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.56px;
  padding: 6px 10px;
  border: 1px solid #2856a8;
  border-radius: 5px;
  margin-bottom: 12px; }

.main__textarea {
  color: #484848;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.56px;
  border: 1px solid #2856a8;
  border-radius: 5px;
  padding: 12px 10px;
  margin-bottom: 15px;
  resize: none; }

.main__button {
  width: 250px;
  height: 40px;
  border: none;
  border-radius: 4px;
  background-color: #3698fb;
  font-family: "Barlow-Bold", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.56px;
  color: #ffffff;
  cursor: pointer; }
  .main__button:hover {
    box-shadow: 0 0 5px #2856a8;
    background-color: #eeeeee;
    color: #2856a8; }

@media (max-width: 1440px) {
  .main__button {
    align-self: center; } }

.social {
  min-width: 530px;
  display: flex;
  list-style: none;
  justify-content: space-between;
  padding: 0;
  align-items: center; }

.social__link {
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
  width: 246px;
  height: 40px;
  text-decoration: none;
  color: #ffffff;
  font-family: inherit;
  font-size: 14px; }
  .social__link:hover {
    box-shadow: 0 0 5px #2856a8;
    border-color: #ffffff; }
  .social__link--fb {
    background-color: #255a9b; }
  .social__link--in {
    background-color: #0076b6; }

.social__link svg {
  position: absolute;
  left: 15px; }

body {
  background-color: #999999;
  margin: 0 auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }
