.main {
  background-color: #eff6ff;
  padding: 0;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  color: #2a4a5c;
}

.main__background {
  min-width: 760px;
  min-height: 1000px;
  background-image: url("../img/bg.jpg");
  background-size: cover;
}

@media (max-width: 1440px) {
  .main__background {
    display: none;
  }
}

.main__content {
  display: flex;
  flex-direction: column;
  margin: 110px 75px;
  align-items: flex-start;
}

.main__desc {
  font-size: 20px;
  margin: 0;
  margin-bottom: 80px;
  color: #7b8a92;
}

.main__social {
  margin: 0;
  margin-bottom: 18px;
}

.main__separator {
  align-self: center;
  font-size: 12px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 100px;
    height: 1px;
    left: -110px;
    top: 7px;
    background-color: #afafaf;
  }
  
  &::after {
    content: "";
    position: absolute;
    width: 100px;
    height: 1px;
    right: -110px;
    top: 7px;
    background-color: #afafaf;
  }
}

.main__form {
  display: flex;
  flex-direction: column;
  min-width: 530px;
}

.main__list-of-types {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.main__email {
  color: #484848;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.56px;
  padding: 6px 10px;
  border: 1px solid #2856a8;
  border-radius: 5px;
  margin-bottom: 12px;
}

.main__textarea {
  color: #484848;
  font-family: "Barlow-Regular", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.56px;
  border: 1px solid #2856a8;
  border-radius: 5px;
  padding: 12px 10px;
  margin-bottom: 15px;
  resize: none;
}

.main__button {
  width: 250px;
  height: 40px;
  border: none;
  border-radius: 4px;
  background-color: #3698fb;
  font-family: "Barlow-Bold", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.56px;
  color: #ffffff;

  cursor: pointer;

  &:hover {
    box-shadow: 0 0 5px #2856a8;
    background-color: #eeeeee;
    color: #2856a8;
  }
}

@media (max-width: 1440px) {
  .main__button {
    align-self: center;
  }
}
