.social {
  min-width: 530px;
  display: flex;
  list-style: none;
  justify-content: space-between;
  padding: 0;
  align-items: center;
}

.social__link {
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
  width: 246px;
  height: 40px;
  text-decoration: none;
  color: #ffffff;
  font-family: inherit;
  font-size: 14px;

  &:hover {
    box-shadow: 0 0 5px #2856a8;
    border-color: #ffffff;
  }
  &--fb {
    background-color: #255a9b;
  }

  &--in {
    background-color: #0076b6;
  }
}

.social__link svg {
  position: absolute;
  left: 15px;
}
